import { ToastrService } from 'ngx-toastr';
import { WeeklyGamesService } from './../../services/weekly-games.service';
import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-winner',
  templateUrl: './winner.component.html',
  styleUrls: ['./winner.component.scss']
})
export class WinnerComponent implements OnInit {

  weekList=[
   
   
  ]
  constructor(
    private weeklyGameService: WeeklyGamesService,
    private toastr: ToastrService
    ) { }

  ngOnInit(): void {
    this.getGames()
;  }

  getGames(){
    this.weeklyGameService.getgameImages().subscribe((data)=>{
      console.log('winner',data);
      if(data.status === 200){
        this.weekList = data.data;
      }
      
    },(error)=>{
      console.log(error);
      
    })
  }

  winnerDetails(week){
    console.log('clicked on ',week);
    if(week.is_winner_selected == 0){
      this.toastr.warning('Please choose winner in Judgement page first')
    }
    
  }
}
